import React from "react";

import { Text, Flex, Box } from "@chakra-ui/layout";
import { useColorMode } from "@chakra-ui/color-mode";
// import { useMediaQuery } from "@chakra-ui/media-query";

export const Footer = () => {
	//const [isNotSmallerScreen] = useMediaQuery("(min-width:600px)")

	const { colorMode } = useColorMode();
  	const isDark = colorMode === "dark";

	return (
		<Flex
			direction="column"
			w="100%"
			// maxWidth={{ base: "50vh", md: "50vh", lg: "50vh", xl: "50vh" }}
		>
			<Box alignSelf="center">
				<Text color={isDark ? "gray.200" : "gray.500"}>
					Thanks For Visiting
				</Text>
			</Box>
			<Box alignSelf="center">
				<Flex
					rounded="lg"
					direction="row"
					mt={4}
					bg="blackAlpha.800"
					h={12}
					w="26vh"
					justify="center"
				>
					<Text color="white" mt={2.5} fontSize="large" fontWeight="bold">
						Deployed By
					</Text>
					{/* <Icon color="white" mr={1} ml={1} mt={2} as={vercel} /> */}
					<Text color="white" mt={2.5} fontSize="large" fontWeight="black">
						â–²Vercel
					</Text>
				</Flex>
			</Box>
		</Flex>
	)
}