import { Button } from "@chakra-ui/button";
import { useColorMode } from "@chakra-ui/color-mode";
import { Image } from "@chakra-ui/image";
import { 
	useDisclosure, 
	Modal,
	ModalOverlay,
	ModalContent,
	ModalHeader,
	ModalFooter,
	ModalBody,
	ModalCloseButton
} from "@chakra-ui/react";
import { Circle, Stack, Flex, Box, Text } from "@chakra-ui/layout";

import { useMediaQuery } from "@chakra-ui/media-query";
import axios from "axios"
import MAIN_URI from "../config"


import React, { useState } from "react";

export const Header = () => {
  const { colorMode } = useColorMode();
  const isDark = colorMode === "dark";
  const { isOpen, onOpen, onClose } = useDisclosure()

  const [Data, setData] = useState();
//   const [ButtonValue, setButtonValue] = useState('Click This');

  const [isNotSmallerScreen] = useMediaQuery("(min-width:600px)");
  React.useEffect(() => { 
	axios.get(MAIN_URI).then(e => {
		const { data } = e.data
		setData(data)
	})
  }, [])
  return (
	<Stack>
	  {isNotSmallerScreen && (
		<Circle
		  position="absolute"
		  bg="#ffc3c3"
		  opacity="0.1"
		  w="300px"
		  h="300px"
		  alignSelf="flex-end"
		/>
	  )}
	  <Flex
		direction={isNotSmallerScreen ? "row" : "column"}
		spacing="200px"
		p={isNotSmallerScreen ? "32" : 0}
		alignSelf="flex-start"
	  >
		<Box mr={isNotSmallerScreen ? 40 : 0} mt={isNotSmallerScreen ? 0 : 16} align="flex-start">
		  <Text fontSize="5xl" fontWeight="semibold">
			Hi, I am
		  </Text>
		  <Text
			fontSize="7xl"
			fontWeight="bold"
			bgGradient="linear(to-r, #7928CA, #FF0080)"
			bgClip="text"
		  >
			Abdul Rahman
		  </Text>
		  <Text color={isDark ? "gray.200" : "gray.500"}>
			Full Stack Developer and A Student.
		  </Text>
		  <Button
		  	boxShadow="sm"
		  	mt={8}
			backgroundColor="#FF0080"
			borderColor="#FF0080"
			color='white'
			_hover={{
				boxShadow: "dark-lg",
				// backgroundColor:"transparent",
				// outlineColor: "#cb2660",
				// borderColor: "#cb2660",
				// color: `${isDark ? "white" : "black"}`s
			}}
			_active={{
				boxShadow: "base",
				// backgroundColor:"transparent",
				// outlineColor: "#cb2660",
				// borderColor: "#cb2660",
				// color: `${isDark ? "white" : "black"}`s
			}}
			_focus={{
				boxShadow: "base",
				// backgroundColor:"transparent",
				// outlineColor: "#cb2660",
				// borderColor: "#cb2660",
				// color: `${isDark ? "white" : "black"}`s
			}}
			onClick={() => {
				onOpen()
			  }}
		  >
			View Projects
		  </Button>
		</Box>
		<Image
		  alignSelf="center"
		  mt={isNotSmallerScreen ? "0" : "12"}
		  mb={isNotSmallerScreen ? "0" : "12"}
		  borderRadius="full"
		  backgroundColor="transparent"
		  boxShadow="lg"
		  boxSize="300px"
		  src={Data ? `https://cdn.discordapp.com/avatars/${Data.discord_user.id}/${Data.discord_user.avatar}.png?size=2048` : 'O_O'}
		  zIndex={1}
		/>
	  </Flex>
	  <Modal isCentered blockScrollOnMount={true} finalFocusRef={<Header />} isOpen={isOpen} onClose={onClose}>
		{<OverlayOne />}
		<ModalContent>
		  <ModalHeader color="#FF0080" fontWeight="bold">Projects</ModalHeader>
		  <ModalCloseButton />
		  <ModalBody>
			<Text>No Projects, Yet.</Text>
		  </ModalBody>
		  <ModalFooter>
		  </ModalFooter>
		</ModalContent>
	  </Modal>
	</Stack>
  );
}

const OverlayOne = () => (
	<ModalOverlay
	  bg='none'
	  backdropFilter='blur(6px)'
	/>
)

